<script>
import { ref, onMounted, watch, $emit } from 'vue'
import { apiGetCityArea, apiGetLongTermCityArea } from "@/api/api"
import { useCityStore2, useUserPositionStore } from '@/store/global'

export default {
    props: {
    },
    setup(props, { emit }) {
        const cityStore = useCityStore2()
        const userPositionStore = useUserPositionStore()
        let cityAreaData = ref([])
        let cityAreaData2 = ref([])
        let city = ref("請選擇縣市")
        let area = ref("請選擇鄉鎮市區")
        let code = ref(1)
        let cityIdx = ref(0)
        let areaIdx = ref(0)
        let codeIdx = ref(1)
        let cityList = ref([])
        let areaList = ref([])
        let codeList = ref([])
        const changeCityIdx = (e) => {
            cityIdx.value = e.target.value
            // console.log(cityIdx.value)
        }
        const changeAreaIdx = (e) => {
            areaIdx.value = e.target.value
            // console.log(areaIdx.value)
        }
        const changeCodeIdx = (e) => {
            codeIdx.value = e.target.value
            // console.log(codeIdx.value)
        }

        //往外傳
        const updateCityIdx = () => {
            emit("cityIdx", cityIdx)
        }
        const updateAreaIdx = () => {
            emit("areaIdx", areaIdx)
        }
        const updateCodeIdx = () => {
            emit("codeIdx", codeIdx)
        }

        onMounted(async () => {
            if (cityStore.cityAreaList2 == undefined) {
                // console.log('沒有')
                // call api
                cityAreaData2.value = await apiGetLongTermCityArea()
                cityStore.cityAreaList2 = cityAreaData2.value
            } else {
                // console.log('有')
                cityAreaData2.value = cityStore.cityAreaList2
            }
            cityAreaData.value = cityAreaData2.value.data.cityArea
            codeList.value = cityAreaData2.value.data.codeList
            
            
            //預設臺中市南屯區 66000 / 66000070
            //方便測試先預設新北板橋 65000 / 65000020
            if (userPositionStore.userPosition.cityId == 0) {
                city.value = 65000
                cityIdx.value = 65000
                areaList.value = cityAreaData.value.filter(item => item.id == cityIdx.value)[0].areas
                area.value = 65000020
                areaIdx.value = 65000020
            } else {
                city.value = userPositionStore.userPosition.cityId_2
                cityIdx.value = userPositionStore.userPosition.cityId_2
                areaList.value = cityAreaData.value.filter(item => item.id == cityIdx.value)[0].areas
                area.value = userPositionStore.userPosition.areaId_2
                areaIdx.value = userPositionStore.userPosition.areaId_2
            }
        })

        watch(() => cityIdx.value, async () => {
            // areaList.value = cityAreaData.value[cityIdx.value - 1].areas
            areaList.value = cityAreaData.value.filter(item => item.id == cityIdx.value)[0].areas
            updateCityIdx()
        })
        watch(() => areaIdx.value, async () => {
            updateAreaIdx()
        })
        watch(() => codeIdx.value, async () => {
            updateCodeIdx()
        })

        return { cityAreaData, city, area, areaList, changeCityIdx, changeAreaIdx, codeList, code, changeCodeIdx }
    },
};
</script>

<template>
    <div class="ui-tab selectArea">
        <div class="row">
            <div class="col">
                <select class="form-control form-select" v-model="city" @change="changeCityIdx">
                    <option selected>請選擇縣市</option>
                    <option v-for="item in cityAreaData" :value="item.id" :key="item.id">{{ item.city_name }}</option>
                </select>
            </div>
            <div class="col">
                <select class="form-control form-select" v-model="area" @change="changeAreaIdx">
                    <option selected>請選擇鄉鎮市區</option>
                    <option v-for="item in areaList" :value="item.id" :key="item.id">{{ item.area_name }}</option>
                </select>
            </div>
        </div>
        <div class="row">
            <div class="col">
                <select class="form-control form-select" v-model="code" @change="changeCodeIdx">
                    <option v-for="item in codeList" :value="item.id" :key="item.id">{{ item.name }}</option>
                </select>
            </div>
        </div>
    </div>
</template>


<style lang="scss" scoped>
.selectArea {
    // border: 10px solid purple;
}
</style>