<script>
import Header from "../../components/global/header/HeaderArea.vue"
import CitySelecter from "./components/CitySelecter_.vue"
import { Toast } from "../../components/global/swal.js"
import { ref, onMounted, watch } from "vue"
// call api
import { apiLongTermCare2 } from "@/api/api"
import { useGlobalStore } from "@/store/global"

export default {
  setup() {
    const globalStore = useGlobalStore()
    const goto = globalStore.goto

    let longTermData = ref([])
    let cityIdx = ref(0)
    let areaIdx = ref(0)
    let codeIdx = ref(1)

    const updateCityIdx = (val) => {
      cityIdx.value = val.value
    }
    const updateAreaIdx = (val) => {
      areaIdx.value = val.value
    }
    const updateCodeIdx = (val) => {
      codeIdx.value = val.value
    }

    watch(
      () => cityIdx.value,
      async () => {
      if( cityIdx.value == 0 || areaIdx.value == 0 || codeIdx.value == 0) return
        longTermData.value = await apiLongTermCare2({
          cityId: cityIdx.value,
          areaId: areaIdx.value,
          code: codeIdx.value,
        })
        if (longTermData.value.data.length == 0) {
          Toast("該區域無資料")
          longTermData.value = []
        } else {
          longTermData.value = longTermData.value.data
        }
      }
    )

    watch(
      () => areaIdx.value,
      async () => {
      if( cityIdx.value == 0 || areaIdx.value == 0 || codeIdx.value == 0) return
        longTermData.value = await apiLongTermCare2({
          cityId: cityIdx.value,
          areaId: areaIdx.value,
          code: codeIdx.value,
        })
        if (longTermData.value.data.length == 0) {
          Toast("該區域無資料")
          longTermData.value = []
        } else {
          longTermData.value = longTermData.value.data
        }
      }
    )
    watch(
      () => codeIdx.value,
      async () => {
      if( cityIdx.value == 0 || areaIdx.value == 0 || codeIdx.value == 0) return
        longTermData.value = await apiLongTermCare2({
          cityId: cityIdx.value,
          areaId: areaIdx.value,
          code: codeIdx.value,
        })
        // console.log(longTermData.value)
        if (longTermData.value.data.length == 0) {
          Toast("該區域無資料")
          longTermData.value = []
        } else {
          longTermData.value = longTermData.value.data
        }
      }
    )

    return { longTermData, updateCityIdx, updateAreaIdx, updateCodeIdx, goto }
  },
  components: {
    Header,
    CitySelecter,
  },
}
</script>

<template>
  <Header title="長照2.0" right="map" path="/map/longtermCareMap" />
  <div class="longtermCare">
    <section class="c-main">
      <CitySelecter @city-idx="updateCityIdx" @area-idx="updateAreaIdx" @code-idx="updateCodeIdx" />
      <div class="resource-container">
        <div class="card" v-for="item in longTermData" :key="JSON.stringify(item?.geometry?.coordinates)" @click="
          goto('routerQuery', '/map/longtermCareMap', {
            query: {
              longitude: item?.geometry?.coordinates?.[0],
              latitude: item?.geometry?.coordinates?.[1],
            },
          })
        ">
          <div class="card-header">
            <a :href="item.properties?.website" class="card-link">
              <i class="far fa-map-marker"></i>
            </a>
          </div>
          <div class="card-body">
            <a :href="item.properties?.website" class="card-link">
              <div class="card-title">{{ item.properties?.name }}</div>
              <div class="card-text">
                {{ item.properties?.address }}<br />
                {{ item.properties?.telephone }}
              </div>
            </a>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.longtermCare {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}
</style>
